<template>
    <div>
        <v-app-bar
            app
            dark
        >
            <div class="d-flex align-center">
                <router-link
                    to="/"
                    class="header_title"
                >
                    <v-app-bar-title>Mot's Diary</v-app-bar-title>
                </router-link>
            </div>

            <v-spacer></v-spacer>

            <strong v-if="isLoggedIn">{{ username }}</strong>
            <router-link
                v-else
                to="/login"
                tag="button"
            >ログイン</router-link>
            <v-btn
                @click="clickLogout"
            >ログアウト</v-btn>
        </v-app-bar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            username: '',
            isLoggedIn: false
        }
    },

    methods: {
        getUserStatus() {
            this.username = this.$store.getters.username
            this.isLoggedIn = this.$store.getters.isLoggedIn
        },

        clickLogout() {
            this.$store.dispatch('logout')
        }
    },

    created() {
        this.getUserStatus()
    }
}
</script>

<style scoped lang="scss">
    .header_title {
        text-decoration: none;
        color: inherit;
    }
</style>