<template>
    <v-container>
        <v-row>
            <v-col
                cols="9"
                id="content"
            >
                <small>{{ updated_at.getFullYear() + '/' + (updated_at.getMonth()+1) +'/' + updated_at.getDate() }}</small>
                <h1 id="content_title">{{ article.title }}</h1>
                <markdown-it-vue class="md-body" :content="article.content"></markdown-it-vue>
            </v-col>
            <v-col
                cols="3"
            >
                <introduction/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import api from '../services/api'
import Introduction from '../components/Introduction'

import MarkdownItVue from 'markdown-it-vue'


export default {
    components: {
        Introduction,
        MarkdownItVue
    },

    data() {
        return {
            article: {},
            updated_at: null,
        }
    },

    methods: {
        getArticle() {
            api.get('/blog/' + this.$route.params.id)
            .then(response => {
                this.article = response.data
                console.log(this.article)
                this.updated_at = new Date(this.article.updated_at)
            })
        }
    },

    created () {
        this.getArticle()
    }
}
</script>

