    #content {
        background-color: #fff;
        border-radius: 5px;
        padding: 30px;

        &_title {
            font-size: 40px;
            font-weight: normal;
        }

        .md-body {
            h1 {
                padding: 0.5em;/*文字周りの余白*/
                color: #010101;/*文字色*/
                background: #eaf3ff;/*背景色*/
                border-bottom: solid 3px #516ab6;/*下線*/
            }
        }
    }
