<template>
    <v-container>
        <v-row>
            <v-col
                cols="8"
                class="d-flex flex-wrap"
            >
                <Article
                    v-for="(blog, key) in blogs"
                    :key="key"
                    :article=blog
                    class="article"
                ></Article>
            </v-col>
            <v-col
                cols="4"
            >
                <Introduction/>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import api from '../services/api'
import Article from '../components/Article'
import Introduction from '../components/Introduction'

export default {
    components: {
        Article,
        Introduction,
    },

    data() {
        return {
            blogs: []
        }
    },

    created() {
        api.get('blog/')
        .then(response => {
            this.blogs = response.data
        })
    }
}
</script>

<style scoped>
    .article {
        width: 50%;
    }
</style>
